import { useCallback } from "react";
import { Alert, Platform } from "react-native";

/**
 * ,      alert  .
 */
export const useConfirm = () => {
  /** Native alert    . */
  const asyncNativeAlert = (
    title: string,
    subTitle?: string
  ): Promise<boolean> => {
    return new Promise((resolve, reject) => {
      Alert.alert(
        title,
        subTitle,
        [
          { text: "", onPress: () => resolve(true) },
          { text: "", onPress: () => resolve(false) },
        ],
        { cancelable: false }
      );
    });
  };

  return useCallback(
    async (title: string, subTitle?: string): Promise<boolean> => {
      if (Platform.OS === "web") {
        return confirm(title + (subTitle ? `\n${subTitle}` : ""));
      } else {
        return await asyncNativeAlert(title, subTitle);
      }
    },
    []
  );
};
