import LikeType, { LikeTypeSelector } from "../../types/Like";
import UserType from "../../types/User";
import VideoType from "../../types/Video";

/**         */
export const MAX_VIDEO_LIKE_LIMIT = 3; //    ?

export enum QueResourceResponseErrorType {
  // Bad Requests
  BadBody = "400",
  AlreadyExist = "409",
  TooManyRequest = "429",
  Wrong = "403",
  NotFound = "404",
  Timeout = "408",
  SignInRequired = "401",
  Gone = "410",
  // Server Error
  UndefinedError = "500",
}

export interface QueResourceResponse<T = void> {
  /**    */
  success: boolean;
  /**   */
  payload?: T;
  /**      */
  errorType?: QueResourceResponseErrorType;
}

