import { StatusBar } from "expo-status-bar";
import { StyleSheet, LogBox, SafeAreaView, Platform } from "react-native";
import React, { useEffect, useState, useCallback } from "react";
import { Entypo } from "@expo/vector-icons";
import * as SplashScreen from "expo-splash-screen";
import * as Font from "expo-font";
import { NavigationContainer } from "@react-navigation/native";
import { bColors } from "./styles/base";
import RootScreen from "./screens/RootScreen";
import { useAuth } from "./hooks/useAuth";
import QueAuthClient from "./api/QueAuthUtils";
import { useNotice } from "./hooks/useNotice";
import { useAppDispatch } from "./hooks/useAppDispatch";
import { useSelector } from "react-redux";
import {
  initializeNoticeList,
  selectNoticeList,
} from "./reducers/noticeReducer";

/**
 *    ,  
 * TBD:   
 */
export default function AppContainer() {
  const [appIsReady, setAppIsReady] = useState(false);
  /**       */
  const { isSigned } = useAuth();

  const dispatch = useAppDispatch();

  /**
   *    useEffect   .
   */
  useEffect(() => {
    /**
     * Splash screen 
     */
    async function prepare() {
      try {
        /**  fetch  splash   */
        await SplashScreen.preventAutoHideAsync();

        //   API   
        /**    */
        await Font.loadAsync(Entypo.font);

        dispatch(initializeNoticeList({ noticeId: "ALPHA01" }));

        /**   */
        if (isSigned) {
          await QueAuthClient.refreshUser();
        }
      } catch (e) {
        console.warn(e);
      } finally {
        //   
        setAppIsReady(true);
      }
    }

    //  
    prepare();
  }, []);

  const onLayoutRootView = useCallback(async () => {
    if (appIsReady) {
      //       
      //  'setAppIsReady'  
      //          .
      //  root view layout       .
      await SplashScreen.hideAsync();
    }
  }, [appIsReady]);

  if (!appIsReady) {
    return null;
  }

  return (
    <SafeAreaView onLayout={onLayoutRootView} style={styles.rootContainer}>
      <NavigationContainer
        linking={{
          enabled: true,
          prefixes: [
            "https://localhost",
            "https://anteater333.github.io/Que-web",
            "que://",
          ],
        }}
      >
        <RootScreen isSigned={isSigned} />
      </NavigationContainer>
      <StatusBar
        translucent={false}
        style="auto"
        backgroundColor={bColors.transparent}
      />
    </SafeAreaView>
  );
}

/**
 *    
 */
const styles = StyleSheet.create({
  rootContainer: {
    width: "100%",
    flex: 1,
    maxWidth: Platform.OS === "web" ? 720 : undefined,
    shadowColor: bColors.black,
    shadowOffset: {
      width: 0,
      height: 0,
    },
    shadowOpacity: 0.5,
    shadowRadius: 50,
    elevation: 5,
  },
});
